.exp {
}
.exp h1 {
  text-align: center;
  /* margin-bottom: 80px; */
  text-transform: uppercase;
  font-weight: 300;
  color: white;
  text-shadow: 1px 1px 4px black;
  padding: 20px 0;
}
.exp_wrap {
  display: flex;
  flex-wrap: wrap;
  max-width: 1270px;
  margin: 0 auto;
  gap: 10px;
  padding: 10px;
}
.card {
  display: flex;
  flex-direction: column;
  flex: 1 1 calc(100% - 20px);
  box-shadow: 1px 1px 4px black;
  padding: 10px;
  background: #fff;
}
.card > * + * {
  margin-bottom: 3px;
}
.card .title {
  font-size: 20px;
  text-transform: uppercase;
  font-weight: 500;
  text-align: center;
}
.card .time {
  color: gray;
  font-size: 16px;
  font-style: italic;
}

.card .job_title {
  font-size: 18px;
  font-weight: bold;
}

.card .job_place {
  font-weight: 300;
  text-shadow: 1px 1px 4px rgb(184, 184, 184);
}
.card ul {
  list-style-position: inside;
  list-style-type: disc;
}
.card li:not(:last-child) {
  margin-bottom: 3px;
}
.exp_outer_wrap {
  background-image: url("./exp_bg.jpg");
  background-blend-mode: multiply;
  background-color: rgba(30, 143, 255, 0.561);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-bottom: 100px;
  /* padding-top: 100px; */
  clip-path: polygon(0 0, 100% 0, 100% 90%, 0 100%);
}
@media (min-width: 700px) {
  .card {
    flex: 1 1 calc(50% - 20px);
  }
}
@media (min-width: 1100px) {
  .card {
    flex: 1 1 calc(100% / 3 - 20px);
  }
}
