form {
  max-width: 400px;
  margin: 0 auto 20px auto;
}
form .form_control {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}
form .form_control input {
  font-size: 20px;
  padding: 5px;
  margin: 5px 0;
}
form .form_control textarea {
  padding: 5px;
  width: 100% !important;
  height: 200px !important;
  resize: none;
  margin: 5px 0 !important;
}
form .error {
  font-size: 14px;
  color: firebrick;
}
form button {
  width: 100%;
  padding: 10px;
  text-transform: uppercase;
  font-size: 20px;
  letter-spacing: 1px;
  color: black;
  border: 2px solid black;
  background: white;
  cursor: pointer;
  transition: all 1s;
  z-index: 1;
  position: relative;
}
form button::after {
  content: "";
  position: absolute;
  width: 0%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgb(30, 144, 255, 0.5);
  z-index: -1;
  transition: width 0.5s;
}
form button:hover::after {
  width: 100%;
}
.contact_me {
  font-size: 32px;
  text-align: center;
  margin-bottom: 30px;
  /* margin-top: 80px; */
  text-transform: uppercase;
  font-weight: 300;
  color: black;
  text-shadow: 1px 1px 4px gray;
}
.c-m {
  padding: 10px;
}
