#Projects {
  padding: 10px;
}
#Projects h1 {
  text-align: center;
  margin-bottom: 30px;
  /* margin-top: 80px; */
  text-transform: uppercase;
  font-weight: 300;
  color: black;
  text-shadow: 1px 1px 4px gray;
}
.all_projects {
  max-width: 1270px;
  margin: 0 auto;
}
.project_card img {
  width: 100%;
  min-height: 200px;
  border-bottom: 2px solid #000;
}
.button_selects {
  max-width: 1270px;
  margin: 0 auto;
  margin-bottom: 20px;
  display: flex;
  gap: 10px;
  overflow-x: scroll;
}
.button_selects button {
  margin-right: 5px;
  padding: 5px;
  font-size: 18px;
}
.project_card {
  margin-bottom: 10px;
  box-shadow: 1px 1px 6px black;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  background: linear-gradient(
    145deg,
    rgb(255, 255, 255) 35%,
    rgba(82, 166, 236, 1) 100%
  );
}
.project_card .info {
  padding: 5px;
  display: flex;
  flex-direction: column;
  height: 100%;
}
.project_card .title {
  font-size: 24px;
  font-weight: 500;
  letter-spacing: -1px;
  margin-bottom: 3px;
}
.project_card span {
  background: white;
  color: black;
  align-self: flex-start;
  border: 2px solid black;
  padding: 3px 10px;
  border-radius: 10px;
  font-size: 14px;
}
.project_card .about {
  margin-top: 5px;
  font-size: 17px;
  letter-spacing: -0.5px;
  margin-bottom: 5px;
}
.project_card a {
  padding: 5px;
  text-decoration: none;
  background: white;
  color: black;
  border: 2px solid black;
  display: inline-block;
  margin-right: 5px;
}
.btn.active {
  border: none;
  /* border: 2px solid black; */
  color: white;
  background: rgb(100, 175, 255);
}
.b_wrap {
  margin-top: auto;
}
.b_wrap button,
.btn {
  cursor: pointer;
}
@media (min-width: 600px) {
  .all_projects {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    width: calc(100% - 10px);
  }
  .project_card {
    flex: 0 1 calc(100% / 2 - 5px);
    margin-bottom: 0;
  }
}

@media (min-width: 900px) {
  /* .all_projects {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    width: 100%;
  } */
  .project_card {
    flex: 0 1 calc(100% / 3 - 7px);
    margin-bottom: 0;
  }
}
