#Skills {
  padding: 10px;
  max-width: 1000px;
  margin: 0 auto;
  margin-bottom: 50px;
  /* background-image: url("./skills_bg.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; */
  /* background-position: center center; */
}
#Skills h1 {
  text-align: center;
  margin-bottom: 30px;
  /* margin-top: 80px; */
  text-transform: uppercase;
  font-weight: 300;
  color: black;
  text-shadow: 1px 1px 4px gray;
}
.skill {
  display: flex;
  flex-direction: column;
  text-align: left;
  background: #fff;
  /* height: 0%; */
  width: 100%;
  box-shadow: 1px 1px 3px black;
  /* border-radius: 10px 0px 10px 10px; */
  position: relative;
  padding: 10px;
  cursor: normal;
  transition: box-shadow 0.5s;
}
.skill::after {
  content: "";
  border-radius: 5px;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 2px 2px 5px black;
  opacity: 0;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.skill:hover::after {
  opacity: 1;
}
.skill h2 {
  position: absolute;
  text-align: center;
  padding-left: 10px;
  padding-right: 10px;
  width: auto;
  right: 0;
  top: 0;
  /* box-shadow: inset 0px 0px 7px black; */
  background: #fff;
}
.ext_title {
  font-weight: bold;
  font-size: 20px;
  letter-spacing: -1px;
  /* margin-top: 22px; */
}
.short_description {
  margin-top: 10px;
}
.all_skills {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: space-around;
}
@media (min-width: 549px) {
  .skill {
    flex: 1 1 calc(100% / 2 - 10px);
  }
}
@media (min-width: 800px) {
  .skill {
    flex: 1 1 calc(100% / 3 - 10px);
  }
}
