header {
  padding: 10px;
  position: fixed;
  width: 100%;
  top: 0;
  background: rgba(250, 250, 250, 0.3);
  z-index: 100;
  transition: background 0.3s;
}
header.scrolled {
  background: rgba(0, 0, 0, 0.8);
}
header.scrolled .page_links.page_links.visible {
  background: rgba(0, 0, 0, 0.8);
}
header .wrap {
  display: flex;
  max-width: 1270px;
  margin: 0 auto;
  justify-content: space-between;
}
.socials {
  display: flex;
  align-items: center;
  gap: 5px;
}
.socials a {
  display: flex;
  align-items: center;
  font-size: 24px;
  color: white;
}
.socials a:hover {
  transition: color 0.5s;
  color: black;
}
.page_links {
  position: fixed;
  left: 100%;
  top: 44px;
  transition: left 0.5s, background 0.3s;
  align-items: center;
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 10px;
}
.page_links.visible {
  left: 0;
  background: rgba(250, 250, 250, 0.3);
  /* transition: all 0.5s; */
}

.page_links a {
  font-size: 18px;
  padding: 5px;
  width: 100%;
  text-align: center;
  text-decoration: none;
  color: white;
  text-shadow: 1px 2px 2px black;
}
.page_links a:hover {
  transition: all 0.5s;
  background: rgba(250, 250, 250, 0.5);
}
.burger {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.line {
  width: 30px;
  height: 3px;
  background: #fff;
  transition: all 0.5s;
}
.line:nth-child(2) {
  margin: 4px 0;
}
.burger.active > .line1 {
  transform: rotate(-45deg) translate(-5px, 5px);
}
.burger.active > .line2 {
  opacity: 0;
}
.burger.active > .line3 {
  transform: rotate(45deg) translate(-5px, -5px);
}

@media (min-width: 576px) {
  .page_links {
    flex-direction: row;
    position: static;
    transform: translateX(0%);
    transition: none;
    width: auto;
  }
  .page_links a {
    padding: 0;
    width: auto;
  }
  .burger {
    display: none;
  }
  .page_links a:hover {
    background: none;
  }
}
